<div class="wrapper vw-100 vh-100"><div class="chart-container" ref="chartContainer"></div><div class="order-book small"><div class="order-book-list order-book-ask"><div class="order-book-row d-flex" v-for="(amount, price) in orderBook.asks"><div class="order-book-col text-danger">{{ price }}</div><div class="order-book-col">{{ amount.toFixed(4) }}</div></div></div><div class="order-book-list order-book-bid"><div class="order-book-row d-flex" v-for="(amount, price) in orderBook.bids"><div class="order-book-col text-success">{{ price }}</div><div class="order-book-col">{{ amount.toFixed(4) }}</div></div></div></div></div>